<template>
   <div>
      <dialogs type="customer" ref="addPricePlan" v-if="showDialog" width="40em" :hasNoPaddB='true' overflow="auto">
        <div slot="header">
          {{operateFlag == 0 ? '添加方案' : showItem == 1 ? '修改项目' : '修改方案'}}
        </div>
        <div slot="content" >
          <ul class="dialog-ul">
            <li v-for="(item,$index) in addList" class=" formList " :class="item.classes" v-if="typeof showItem == 'undefined' || showItem == 1">
              <label v-if="!item.isEmpty" class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span> {{item.label}} </label>
              <label v-if="item.isEmpty" class="label-show" style="margin-left: 0;"><span >&nbsp;</span>{{item.label}} </label>
              <inputs v-if="item.type === 'input'" :isEmpty="item.isEmpty"  :option="item.options" @getInput="getFrom" :ref="item.options.key"></inputs>
              <selects v-if="item.type === 'select'" :option="item.options" @getSelect="getFrom" :ref="item.options.key"></selects>
            </li>
            <template v-if="typeof showItem == 'undefined' || showItem == 2">
              <li class="formList halfWidth" style="width: calc(75% - 2em)!important">
               <label class="label-show" style="margin-left: 0;"><span class="emptyTips"> * </span>临充配置 </label>
               <inputs   :option="priceType[0].options[1].option" @getInput="getFrom" ></inputs>
               <label class="label-show" style="min-width:0.8em;margin-left:0"> - </label>
               <selects  :option="priceType[0].options[0].option" @getSelect="getFrom"></selects>
               <!-- <label class="label-show" style="min-width:0.8em;margin-left:0">&nbsp; 元 </label> -->
             </li>
             <li class="formList halfWidth" style="width: calc(25% + 0.5em) !important;min-width: 4em!important;">
               <button class="button-default dialogFromLB"  @click="addImmPrice">
                  <i class="hasIcon " style="margin-top:4px;">
                      <icon name="plus-circle" scale="1.4"></icon>
                   </i>添加
               </button>
             </li>
             <li class="allWidth" v-if="getPlanLen() > 0">
               <label class="label-show" style="margin-left: 0;"></label>
               <span  class="formLiPriceSpan" v-for="(theItem, $indexs) in temporary" v-if="theItem.flag != '1'">
                   <template v-if="theItem.time != '-1'">
                     {{theItem.price}}元/{{theItem.time}}小时
                   </template>
                   <template v-if="theItem.time == '-1'">
                     {{theItem.price}}元/一次充满
                   </template>
                   <span class="removePrice" @click="removeImmPrice($indexs)"> × </span>
               </span>
             </li>
             <li>
               <label class="label-show" style="margin-left: 0;"><span class="emptyTips">   </span>月充配置 </label>
               <input type="checkbox"  class="checkBox dialogFromCheck"  style="float:left" @click="hasMonthPlan($event)" :checked="MonthPlanFlag" />
               <span class="inputChoose myCheckbox myCheckBox dialogFromCheckSpan">启用</span>
             </li>
             <li  class="formList halfWidth" style="width: calc(75% - 2em)!important" v-show="MonthPlanFlag">
               <label class="label-show" style="margin-left: 0;"> </label>
               <inputs   :option="priceType[1].options[1].option" @getInput="getFrom" ></inputs>
               <label class="label-show" style="min-width:0.8em;margin-left:0"> - </label>
               <selects  :option="priceType[1].options[0].option" @getSelect="getFrom"></selects>
              <!--  <label class="label-show" style="min-width:0.8em;margin-left:0">&nbsp; 元 </label> -->
             </li>
             <li class="formList halfWidth" style="width: calc(25% + 0.5em) !important;min-width: 4em!important;" v-show="MonthPlanFlag">
               <inputs   :option="priceType[2].options" @getInput="getFrom" ></inputs>
             </li>
            </template>
           
          </ul>
        </div>
        <div slot="footer">
          <button class="button-default" @click="submitData">提交</button>
          <button class="button-default cancel-button" @click="cancelDialog">取消</button>
        </div>
      </dialogs>
      <dialogs type="alert" ref="alerts" ></dialogs>

   </div>
</template>
<script>
  export default{
    data() {
      return {
         showDialog: false,
         formData: {
           companyName: '',
           projectAddr: '',
           projectName: '',
           basePrice: '',
           upPrice: '',
           communityName: '',
           contact: '',
           contactTelphone: '',
           remark: ''
         },
         immPrice: {
            time: '',
            price: ''
         },
         monthly: {
            monthlyTime: '',
            monthlyPrice: '',
            monthlyCnt: ''
         },
        temporary: [],
        MonthPlanFlag: false
      }
    },
    props: ['priceData', 'operateFlag', 'showItem'],
    computed: {

       addList: function () {
        
         let width = 'calc(100% - 6.5em)';
            return [
                {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '项目名称', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    value: this.formData.projectName,
                    length: 50,
                    key: 'formData.projectName'
                    }
                },
              
                {  
                  classes: 'halfWidth ',
                  type: 'input', 
                  isEmpty: false, 
                  label: '基础电价', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    placeHolder: '大于0且小于99.99，允保留两位小数', 
                    errorMsg: '基础电价： 大于0且小于99.99，允保留两位小数',
                    length: 5,
                    dataType: '+float',
                    limit: 'float',
                   // regular: 'float4',
                    key: 'formData.basePrice',
                    value: this.formData.basePrice
                  }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: false, 
                  label: '提成电价', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    placeHolder: '0-99.99之间，允保留两位小数', 
                    value: this.formData.upPrice || 0,
                    length: 5,
                    //dataType: '+float',
                    limit: 'float',
                    key: 'formData.upPrice'
                  }
                },
                 {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '小区名称', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    value: this.formData.communityName,
                    length: 100,
                    key: 'formData.communityName'
                    }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: true, 
                  label: '物业名称', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    value: this.formData.companyName,
                    length: 200,
                    key: 'formData.companyName'
                    }
                },
                {  
                  classes: 'halfWidth',
                  type: 'input', 
                  isEmpty: true, 
                  label: '联系人员', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    value: this.formData.contact,
                    length: 50,
                    key: 'formData.contact'
                    }
                },
                {  
                  classes: 'halfWidth hasMarginLi',
                  type: 'input', 
                  isEmpty: true, 
                  label: '联系电话', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    value: this.formData.contactTelphone,
                    length: 11,
                    regular: 'phone',
                    errorMsg: '联系电话： 11位手机号码',
                    key: 'formData.contactTelphone'
                    }
                },
                 {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: false, 
                  label: '小区地址', 
                  options: {
                    type: 'normalInput', 
                    width: width,  
                    value: this.formData.projectAddr,
                    length: 200,
                    key: 'formData.projectAddr'
                    }
                },
                {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: true, 
                  label: '备注信息', 
                  options: {
                    type: 'normalInput', 
                    value: this.formData.remark,
                    width: width,  
                    placeHolder: '50个字符内', 
                    key: 'formData.remark'
                   }
                }
            ]
       },
       priceType: function () {
       
         let width = 'calc((100% - 9.5em) / 2)';
         let widths = 'calc(100% - 1.5em)';
         return [
            {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: true, 
                  label: '临充配置',
                  options: [
                    {    type: 'select',
                         option:  {
                            type: 'select',
                            key: 'immPrice.time',
                            cs: 'client',
                            data: this.makeUpTimeSelect(25, false, true),
                           // orgin: 'top',
                            width: width,
                         }
                    },
                    {    type: 'input',
                         option:  {
                            type: 'normalInput',
                            key: 'immPrice.price',
                            width: width,
                            value: this.immPrice.price,
                            placeHolder: '金额，大于0且小于9999999.99，允许两位小数', 
                            errorMsg: '临充配置金额： 大于0且小于9999999.99，允许两位小数',
                            length: 10,
                            regular: 'float7',
                            dataType: '+float',
                            limit: 'float'
                         }
                    }
                  ]    
            },
            {  
                  classes: 'allWidth',
                  type: 'input', 
                  isEmpty: true, 
                  label: '包月配置',
                  options: [
                    {    type: 'input',
                         option:  {
                            type: 'select',
                            key: 'monthly.monthlyTime',
                            width: width,  
                            cs: 'client',
                            constant: this.monthly.monthlyTime == '' ? '' : {value: this.monthly.monthlyTime},
                            data: this.makeUpTimeSelect(25, true),
                           //  orgin: 'top'
                         }
                    },
                    {    type: 'input',
                         option:  {
                            type: 'normalInput',
                            key: 'monthly.monthlyPrice',
                            width: width,  
                            placeHolder: '金额，大于0且小于9999999.99，允许两位小数', 
                            value: this.monthly.monthlyPrice,
                            length: 10,
                            errorMsg: '包月配置单次金额： 大于0且小于9999999.99，允许两位小数',
                            regular: 'float7',
                            dataType: '+float',
                            limit: 'float'
                         }
                    }
                  ]   
            },
            {    type: 'input',
                 options:  {
                            type: 'normalInput',
                            key: 'monthly.monthlyCnt',
                            width: widths,  
                            placeHolder: '充电次数，1-40之间的整数', 
                            value: this.monthly.monthlyCnt,
                            length: 2,
                            limit: 'number'
                 }
            }
         ]
       }
     },
    methods: {
        hasMonthPlan: function (event) {
           this.MonthPlanFlag = event.target.checked;
        },
        removeImmPrice: function(index) {
          let item = this.temporary[index];
          if(item.flag == '0') {
             this.temporary.splice(index, 1)
          } else {
            item.flag = '1';
            this.temporary.splice(index, 1, item);
          }
        },
        getPlanLen: function () {
          let len = 0;
          for(let i=0, l=this.temporary.length; i<l; i++) {
            if(this.temporary[i].flag != '1') {
                len ++;
            }
          }
          return len
        },
        addImmPrice: function () {
   
          if(parseFloat(this.immPrice.price) == 0 || this.immPrice.price == '') {
            this.$refs.alerts.show({msg: '临充配置的金额不能为空且大于0！'})
            return
          }
         
          if(this.getPlanLen() > 2) {
            this.$refs.alerts.show({msg: '临充配置最多可以添加3项！'})
            return
          }
          for(let i=0, l=this.temporary.length; i<l; i++) {
            if(this.temporary[i].time == this.immPrice.time && this.immPrice.price == this.temporary[i].price) {
                if(this.temporary[i].flag != '1') {
                   this.$refs.alerts.show({msg: '该方案已存在，请勿重复添加！'})
                   return
                }
            }
          }
          
          this.temporary.push({time: this.immPrice.time, price: this.immPrice.price, flag: '0'});
          this.immPrice = this.cleanSearchData(this.immPrice);
        },
        makeUpTimeSelect: function (time, hasNoChoose, hasOnceOver) {
           let arr = [];
           for(let i =1; i< time; i++) {
                arr.push({text: i + '小时', value: i})
           }
           if(hasNoChoose) {
              arr.unshift({text: '空选项', value: ''})
           }
           if(hasOnceOver) {
              arr.unshift({text: '一次充满', value: '-1'})
           }
           return arr
        },
        cancelDialog: function ()  {
            this.showDialog = false;
            this.formData = this.cleanSearchData(this.formData);
            this.monthly = this.cleanSearchData(this.monthly);
            this.temporary = [];
            this.$refs.addPricePlan.close()
        },
        showOperate: function() {
            this.getMonthFlag();
            this.copyData();
            this.showDialog = true;
            let that = this;
            setTimeout(function () {
              that.$refs.addPricePlan.show()
            }, 0)
        },
        getFrom: function (val) {
              this.getOption(val, true);
        },
        submitData: function () {
           let that = this;
           let alerts = that.$refs.alerts;
           that.regularTest({  
              emptyKey: that.showItem == '2' ? [] : ['projectName', 'projectAddr', 'basePrice', 'upPrice', 'communityName'],
              prefix: 'formData',
              callback: function () {
                  if(parseFloat(that.formData.basePrice) == 0 || parseFloat(that.formData.basePrice)  >= 100) {
                      alerts.show({msg: '基础电价应大于0且小于100 !'});
                      return 
                  }
                  if(that.getPlanLen() == 0) {
                     alerts.show({msg: '临时配置方案不能为空！'});
                     return 
                  }
                  let price = parseFloat(that.monthly.monthlyPrice);
                  let limit = parseFloat(that.monthly.monthlyCnt);
                  let time = parseFloat(that.monthly.monthlyTime);
                  if(!that.MonthPlanFlag || (that.MonthPlanFlag && price > 0 && limit > 0 && time  > 0)) {
                    if(limit > 40) {
                       alerts.show({msg: '月充配置的充电次数不能大于40'});
                       return 
                    } else {
                       let param = that.formData;
                       if(that.operateFlag == 0) {
                         param.temporary = that.temporary;
                       } else {
                         let arr = [];
                         for(let i=0, l=that.temporary.length; i<l; i++) {
                           if(typeof that.temporary[i].flag != 'undefined') {
                              arr.push(that.temporary[i])
                           }
                         }
                         param.temporary= arr;
                       }
                       if(that.operateFlag == 0 && that.MonthPlanFlag) {
                           param.monthly = that.monthly;
                       }
                      
                       if(that.operateFlag == 1) {
                         let f = typeof that.priceData.monthly != 'undefined' && JSON.stringify(that.priceData.monthly).toString() != '{}';
                         if(f && !that.MonthPlanFlag) {
                           param.monthly = that.monthly;
                           param.monthly.flag = 1;
                         }  else if(!f && that.MonthPlanFlag) {
                            param.monthly = that.monthly;
                            param.monthly.flag = 0;
                         }else if (f && that.MonthPlanFlag) {
                            if(!(that.priceData.monthly.monthlyTime == that.monthly.monthlyTime &&
                              that.priceData.monthly.monthlyPrice == that.monthly.monthlyPrice && 
                              that.priceData.monthly.monthlyCnt == that.monthly.monthlyCnt)) {
                                 param.monthly = that.monthly;
                                 param.monthly.flag = 2;
                            }
                         }
                       }
                       that.$emit('priceData', that.getSearchData(param));
                       that.cancelDialog();
                    }
                  } else {
                       if(isNaN(price) || isNaN(limit) || isNaN(time)){
                         alerts.show({msg: '请将月充配置中的参数填写完整!'});
                         return 
                       } else {
                         alerts.show({msg: '月充配置中的参数均大于0 !'});
                       }
                  }  
              }
            })
        },
        copyData: function() {
          if(this.operateFlag == 1) {
            for(let key in this.formData) {
              this.formData[key] = this.priceData[key] || ''
            }
            if(this.priceData['monthly']) {
               for(let key in this.monthly) {
                 this.monthly[key] = this.priceData['monthly'][key] || ''
               }
               this.monthly.schemeGuid = this.priceData['monthly']['schemeGuid'] || ''
            }
            this.temporary = this.priceData.temporary.slice(0);
            for(let i=0,l=this.temporary.length; i<l;i++) {
              if(this.temporary[i].payCategory == 3) {
                this.temporary[i]['time'] = '-1'
              }
            }
          }
        },
        getMonthFlag: function () {
           if(this.operateFlag == 0 || (this.operateFlag == 1 && typeof this.priceData.monthly == 'undefined')) {
              this.MonthPlanFlag = false
           }else if(this.operateFlag == 1 && typeof this.priceData.monthly != 'undefined' && JSON.stringify(this.priceData.monthly).toString() != '{}') {
             this.MonthPlanFlag = true
           }
        }
    },
    created: function() {
       this.getMonthFlag()
       this.copyData()
    },
    watch: {
       priceData(val, oldVal) {
       
       }
    }
  }
</script>
<style lang="scss">
 
</style>


