<template>
    <div  class="info-div" style="100%">
        <div class="info-header ">
          <span class="callback" @click="resetComponent">充电方案配置 </span>
          <span> > 方案配置详情</span>
        </div>
        <div class="info-container" >
            <div  class="list-content detail-content  list-contentB" >
              <div class="list-header " style="border:0">
                 <i class="hasIcon hasImg">
                  <img src="dist/img/widsomApp/basic.png" />
                 </i>基础信息
                 <button class="list-operate " @click="deletePlan" v-if="controls[3] || true">
                    <i class="hasIcon ">
                      <icon name="trash" ></icon>
                    </i>删除
                 </button>
                 <button class="list-operate" @click="editPlan" v-if="controls[2] || true">
                    <i class="hasIcon ">
                      <icon name="edit" ></icon>
                    </i>编辑方案
                  </button>
                  <button class="list-operate" @click="editProject" v-if="controls[2] || true">
                    <i class="hasIcon ">
                      <icon name="edit" ></icon>
                    </i>编辑项目
                  </button>
              </div>
              <div class="lInfoCon">
                 <div class="infoL">
                   <div class="infoLCon">
                     <img src="dist/img/analyse/electric.png"/>
                     <div>
                        <span class="hasColor">{{row.deviceNum}}</span>
                        <span >设备数量</span>
                     </div>
                   </div>
                 </div>
              </div>
              <ul class="form-list rInfoCon" style="padding:1em">
                     <li v-for="(item,$index) in commInfoShow" style="margin:0!important" :style="{width: item.widths ? item.widths : ''}">
                         <span class="label-span">{{item.text}}</span>
                         <span class="label-span" style="max-width: calc(100% - 8em);" :title="item.value"> &nbsp;:&nbsp;&nbsp;{{item.value}}</span>
                     </li>
              </ul>
            </div>
            <div  class="list-content detail-content  list-contentB" style="height:calc(100% - 15em)">
             <!--  <div class="list-header" style="border:0;margin-top:1em">
                 <i class="hasIcon hasImg">
                  <img src="dist/img/face/QR.png" />
                </i>营收分析
              </div> -->
              <div class="chartCon" style="height:calc(100% - 1em);margin-top:1em">
                <ul class="simpleTopTab">
                   <li v-for="(item, $index) in tabs" @click="getTab(item,$index)" :class="{selected: tabIndex == $index}">
                    {{item.titles}}
                  </li>
               </ul>
               <div class="tabCenContent">
                   <div class="lpiechart">
                    <div  class="pieLegendDiv">
                       <div class="legendCon">
                         <div class="legendSpanCon" v-for="(item, $index) in chartType" @click="changeType($index)">
                           <span class="legendSpanBefore" :style="{background: $index != typeIndex ? 'rgba(255,255,255,0.4)' : currentColor}"></span>
                           <span class="legendSpan" >{{item.name}}</span>
                         </div>
                       </div>
                    </div>
                    <div ref="pieChart" class="piechart"></div>
                   </div>
                  <div class="rtableDetail" >
                     <p>tip: 请从左侧饼图点击查看对应方案的营收详情  {{currentClickPlan!='' ? '-->' : ''}}  <span style="color: #fbd63f">{{currentClickPlan!='' ? '当前选中方案为： ' + currentClickPlan : ''}}</span></p>
                     <div class="table-container" ref="myOwnTable" style="height: calc(100% - 3em)">
                       <tables ref="myTable" :tableOpt="tableOpt" class="tablePadding" v-if="tableShow" ></tables>
                     </div>
                  </div>
               </div>
              </div>
            </div>
        </div>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="confirm" ref="confirms"></dialogs>
        <operateDia @priceData="submitProject" ref="editProject" operateFlag="1" :priceData="commInfo" v-if="showInfo" showItem="1"></operateDia>
        <operateDia @priceData="submitPlan" ref="editPlan" operateFlag="1" :priceData="commInfo" v-if="showInfo" showItem="2"></operateDia>
    </div>
</template>
<script>
    import operateDia from './addOrEdit.vue'
    export default{
        data() {
            return {
               chartType: [
                  {name: '盈利', key: 'profitable', unit: '（元）'},
                  {name: '次数', key: 'useNum', unit: '（次）'},
                  {name: '收入', key: 'deductMoney', unit: '（元）'},
                  {name: '电量', key: 'usePower', unit: '（KWH）'},
                  {name: '成本', key: 'consumptionMoney', unit: '（元）'}
               ],
               typeIndex:0,
               currentClickPlan:'',
               showInfo: false,
               commInfo: {},
               editData: {
                  planName: '',
                  basePrice: '',
                  upPrice: '',
                  remark: ''
                },
               rangeData: {
                   overCurrent: '', overVoltage: '', underVoltage: '', underCurrent: '', underCurrentDelay: ''
               },
             
               ports: [
                 {port: 1, QR: '', runkey: 'runState1', overCurrent: '', overVoltage: '',underVoltage: '', underCurrent: '', underCurrentDelay: ''},
                 {port: 2, QR: '', runkey: 'runState2', overCurrent: '', overVoltage: '', underVoltage: '', underCurrent: '', underCurrentDelay: ''}
               ],
               currentPort: '',
               heartData: '',
               tabs: [
                  {titles: '近一周营收情况', status: 1},
                  {titles: '近一个月营收情况', status: 2},
                  {titles: '近三个月营收情况', status: 3}
               ],
               pie:'',
               tabIndex: 0,
               tableShow: false,
               tableOpt: {
                 column: [
                    {title: '端口号', field: 'port', textAlign: 'center', width: '100'},
                    {title: '类别', field: 'carCategory',textAlign: 'center',width: '130', type: 'string',
                      formatter: function (val) {
                        let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                        switch(vals) {
                           case '1':
                              return '<div >电瓶车</div>'
                           case '2':
                              return '<div>电动车</div>'
                           default:
                              return '<div >--</div>'
                        }

                    }},
                    {title: '计费方式', field: 'payCategory', textAlign: 'center', width: '120', type: 'string',
                      formatter: function (val, index, item, obj) {
                        let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                        return obj.$parent.$parent.getEnumItem('payCategory', vals).text
                        
                    }},
                    {title: '充值费用 (元)', field: 'payMoney', textAlign: 'center', width: '130'},
                    {title: '使用电量 (KWH)', field: 'usePower', textAlign: 'center', width: '130'},
                    {title: '盈利费用 (元)', field: 'profitable', textAlign: 'center', width: '130'},
                    {title: '状态', field: 'state',textAlign: 'center',width: '130', type: 'string',
                      formatter: function (val) {
                        let vals = val !== null && typeof val !== 'undefined' ? val.toString() : '';
                        switch(vals) {
                           case '0':
                              return '<div class="red">正在充电</div>'
                           case '1':
                              return '<div class="green">已完成</div>'
                           default:
                              return '<div >--</div>'
                          }
                    }},
                    {title: '充电时长 (分钟)', field: 'useTime', textAlign: 'center', width: '130'},
                    {title: '开始时间', field: 'startTime', textAlign: 'center', width: '140'},
                    {title: '结束时间', field: 'endTime', textAlign: 'center', width: '140'},
                    {title: '人员ID', field: 'customerGuid', textAlign: 'center', width: '150'}
                 ],
                //  url: 'charger/deviceDetialUseList',
               //   method: 'post',
                  data: [],
                  pageSize: 20,
                  pageNumber: 1,
                  pageList: [20, 40, 60],
                  queryParam: {},
                  isExport: true,
                  exportURL: 'chargerScheme/downShemeUseList'
              },
              colors: [  '#B64439',  '#1562AA', '#5EAEE3', '#FC6655', '#72B8BB','#72A273', '#E97D53', '#8CC0A9',  '#E59C87', '#74999F', '#DC6A65','#FEDA5E',  '#ca8622', '#bda29a','#6e7074', '#546570', '#c4ccd3'],
              pieData: [],
            }
        },
        components: {
          operateDia
        },
        props: ['row', 'controls'],
        methods: {
            changeType: function (index) {
               this.typeIndex = index;
               this.getPieData()
            },
            getTab: function (item, index) {
               this.makePie(item.status);
               this.tabIndex = index
            },
            makePie: function (flag) {
              let option = {
                  color: ['#1562AA','#c23531','#A9D86E', '#5EAEE3', '#FC6655',  '#61a0a8', '#d48265', '#91c7ae','#749f83',  '#ca8622', '#bda29a','#6e7074', '#546570', '#c4ccd3'],
                  tooltip: {
                    trigger: 'item',
                    formatter: function (param) {
                      let data = param.data;
                      return param.name + '： <br/>使用：' + data.useNum + ' 次 <br/>收入：'  + data.deductMoney +' 元 <br/>电量：'  + data.usePower + ' KWH <br/>成本：'  + data.consumptionMoney + ' 元 <br/>盈利：'  + param.value + ' 元 <br/>占比：'  + param.percent + '%'
                    }

                  },
                  legend: {
                    show: false,
                  },
                  series: [
                   {
                     name:'盈利（元',
                     type:'pie',
                     silent: true,
                     selectedOffset: 0,
                     selectedMode: 'single',
                     radius: [0, '30%'],
                     label: {
                      normal: {

                        position: 'inner',
                       /* formatter: function (param) {
                          return param.name + ': ' + param.value + ' (' + param.percent + '%)'
                        },*/
                        formatter: '{b|{b}\n}{c|{c}\n(}{per|{d}%)}',
                        rich: {
                           
                            b: {
                                align: 'center',
                                color: '#fff',
                                fontSize: 12,
                              
                            },
                            c: {
                                //color: '#ff8811',
                                 color: '#fff',
                                fontSize: 12
                            }
                        }
                      }
                     },
                     labelLine: {
                      normal: {
                        show: false
                      }
                     },
                     data:[]
                   },
                   {
                     name:'盈利（元）',
                     type:'pie',
                    
                     radius: ['35%', '50%'],
                     label: {
                      normal: {
                        formatter: '{a|{a}}{per|{d}%}{abg|}\n{hr|}\n  {b|{b}：}{c|{c}} ',
                        backgroundColor: 'rgba(50,98,167,0.9)',
                        borderColor: 'rgba(50,98,167,0.9)',
                        borderWidth: 1,
                        borderRadius: 4,
                        color: 'rgba(255,255,255,0.8)',
                        rich: {
                          a: {
                            color: 'rgba(255,255,255,0.8)',
                            lineHeight: 22,
                            align: 'center'
                          },

                          hr: {
                            borderColor: 'rgba(255,255,255,0.8)',
                            width: '100%',
                            borderWidth: 0.5,
                            height: 0
                          },
                          b: {
                            fontSize: 12,
                            lineHeight: 25,
                            color: 'rgba(255,255,255,0.8)'
                          },
                           c: {
                             fontSize: 12,
                             lineHeight: 25,
                             color: 'rgba(255,255,255,0.8)'
                          },
                          per: {
                            color: '#eee',
                            backgroundColor: '#334455',
                            padding: [2, 4],
                            borderRadius: 2
                          }
                        }
                      }
                     },
                     data:[]
                   }
                ]
              };
             
              let that = this;
              that.getRequest({
                url: 'chargerScheme/countPofitable',
                method: 'post',
                param: {projectGuid: that.row.projectGuid, countType: flag, pageNumber:1, pageSize: 999999999},
                success: function(data) {
                   let Data = data.data;
                   let types = [1,2,3,4];
                   let inner = [];
                   that.pieData = Data;
                   that.getPieData()
                  /* for(let i=0, l= types.length; i<l; i++) {
                      let num = 0;
                      for(let j=0, l1= Data.length; j<l1; j++) {
                          if(types[i] == Data[j].payCategory) {
                             num += Data[j].value
                          }
                      }
                      inner.push({value: num, typees: types[i]})
                   }
                   let mon = 0, imm = 0, innerData = [];
                   for(let i = 0,l=inner.length; i<l;i++) {
                       if(inner[i].typees == 2) {
                   
                         mon += inner[i].value
                       } else {
                        imm += inner[i].value
                       }
                   }
                   innerData.push({name: '月卡', value: mon});
                   innerData.push({name: '临充', value: imm});
                   option.series[0].data = innerData;
                   option.series[1].data = Data;*/
                   
                }
              })
            },
            getPieData: function () {
              let current = this.chartType[this.typeIndex];
              let options = {
                color: this.colors,
                tooltip : {
                   trigger: 'item',
                   formatter: function (param) {
                      let data = param.data;
                      return param.name + '： <br/>使用：' + data.useNum + ' 次 <br/>收入：'  + data.deductMoney +' 元 <br/>电量：'  + data.usePower + ' KWH <br/>成本：'  + data.consumptionMoney + ' 元 <br/>盈利：'  + param.value + ' 元 <br/>占比：'  + param.percent + '%'
                    }
                },
                legend: {
                  show: false
                },
                toolbox: {
                  show : false
                },
                calculable : true,
                series : {
                   name: current.name + current.unit,
                   type:'pie',
                   radius : ['0%', '50%'],
                   center : ['50%', '50%'],
                  // roseType : 'radius',
                   selectedMode: 'single',
                   selectedOffset: 30,
                   clockwise: true,
                   label: {
                      normal: {
                        formatter: '{a|{a}}{per|{d}%}{abg|}\n{hr|}\n  {b|{b}：}{c|{c}} ',
                        backgroundColor: 'rgba(50,98,167,0.9)',
                        borderColor: 'rgba(50,98,167,0.9)',
                        borderWidth: 1,
                        borderRadius: 4,
                        color: 'rgba(255,255,255,0.8)',
                        rich: {
                          a: {
                            color: 'rgba(255,255,255,0.8)',
                            lineHeight: 22,
                            padding:[5, 20],
                            align: 'center'
                          },

                          hr: {
                            borderColor: 'rgba(255,255,255,0.8)',
                            width: '100%',
                            borderWidth: 0.5,
                            height: 0
                          },
                          b: {
                            fontSize: 12,
                            lineHeight: 25,
                            color: 'rgba(255,255,255,0.8)'
                          },
                           c: {
                             fontSize: 12,
                             lineHeight: 25,
                             color: 'rgba(255,255,255,0.8)'
                          },
                          per: {
                            color: '#eee',
                            backgroundColor: '#334455',
                            padding: [2, 4],
                            borderRadius: 2
                          }
                        }
                      }
                   },
                   itemStyle: {
                    normal: {
                       color: this.currentColor,
                       borderColor: 'white',
                       shadowBlur: 200,
                       shadowColor: 'rgba(0, 0, 0, 0.5)'
                     }
                   },
                   labelLine: {
                      normal: {
                        lineStyle: {
                          color: 'rgba(255, 255, 255, 0.3)'
                        },
                        smooth: 0.2,
                        length: 10,
                        length2: 20
                     }
                  },
                
                  data: []
                }
              };
              let Data = this.pieData;
              let key = current.key;

              for(let j=0, l1= Data.length; j<l1; j++) {
                        let item = Data[j];
                        let name = '';
                        switch(item.payCategory) {
                           case 2: 
                             name = '月卡';
                             break;  
                           case 1: 
                             name =  item.money + '元/' + item.chargingTime + '小时';
                             break;  
                           case 3: 
                             name = '一次充满';
                             break;  
                        }
                        Data[j].name = name;
                        Data[j].value = item[key]
              }
              let that = this;
              options.series.data = Data.sort(function (a, b) { return a.value - b.value; })
              that.pie =  echarts.init(that.$refs.pieChart);
              that.pie.setOption(options, true);
              that.pie.on('click', function (params) {
                  let param = {};
                  that.currentClickPlan = params.data.name;
                  param.schemeGuid = params.data.schemeGuid;
                  param.countType = that.tabs[that.tabIndex].status;
                  param.pageNumber = 1;
                  param.pageNumber = 20;
                  param.projectGuid = that.row.projectGuid;
                  that.$refs.myTable.resetTableOpt({
                          queryParam: param,
                          url: 'chargerScheme/shemeUseList',
                          method: 'post',
                       })
                  });
            },
            getFrom: function (val) {
               this.getOption(val, true);
            },
            submitProject: function (param) {
                let that = this;
                delete param.monthly;
                delete param.temporary;
                param.projectGuid = that.row.projectGuid;
                that.getRequest({
                        url: 'chargerScheme/editProject',
                        method: 'post',
                        param: param,
                        success: function (data) {
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                          for(let key in param) {
                            that.$set(that.commInfo, key, param[key]);
                          }
                        },
                       error: function (data) {
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                       }
                })
            },
            submitPlan: function (param) {
                let that = this;
                let params= {};
                params.monthly = param.monthly;
                params.temporary = param.temporary;
                params.monthly = param.monthly;
                params.projectGuid = that.row.projectGuid;
               // console.log(params)
                that.getRequest({
                        url: 'chargerScheme/editScheme',
                        method: 'post',
                        param: params,
                        success: function (data) {
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                          delete that.commInfo.temporary;
                          delete that.commInfo.monthly;
                        
                          that.getDeviceInfo();
                        },
                       error: function (data) {
                          that.$refs.alerts.show({
                             msg: data.message
                          });
                       }
                })
            },
          
            editPlan: function () {
               this.$refs.editPlan.showOperate()
            },
            editProject: function () {
               this.$refs.editProject.showOperate()
            },
            deletePlan: function () {
              let arr = [];
              arr.push(this.row.projectGuid);
              let that = this;

              that.$refs.confirms.show({
                 msg: `确定要删除该方案吗？`,
                 action: {
                   confirm: function () {
                     that.getRequest({
                       url: 'chargerScheme/deleteProject',
                       method: 'post',
                       param: arr,
                       success: function (data) {
                          that.$refs.alerts.show({msg: data.message})
                          that.resetComponent();
                       },
                       error: function (data) {
                        that.$refs.alerts.show({msg: data.message})
                       }
                     })
                   },
                   cancel: function () {

                   }
                 }
              });
            }, 
           //返回列表
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            getDeviceInfo: function () {
              let that = this;
              that.getRequest({
                 url: 'chargerScheme/queryDetail',
                 method: 'post',
                //url: 'dist/json/pricePlan.json',
               // method: 'get',
                param: {projectGuid: that.row.projectGuid},
                success: function (data) {
                  for(let key in data.data) {
                    that.$set(that.commInfo, key, data.data[key])
                  }
                  that.showInfo = true
                }
              }, true)
            }
        },
        computed: {
           currentColor: function () {
                return this.colors[this.typeIndex]
           },
           confirms: function () {
              return this.$refs.confirms
           },
           immPrice: function () {
             let allData = this.commInfo.temporary;
             let showData = [];
             for(let i=0; i<allData.length;i++) {
               if(allData[i].payCategory == 3){
                 showData.push(`${allData[i].price}元/一次充满`)
               } else {
                 showData.push(`${allData[i].price}元/${allData[i].time}小时`)
               }
             }
             return showData.join(" ; ")
           },
           commInfoShow(){
            let allWidth = 'calc(100% - 1em)';
            if(this.showInfo) {
              return [
                {text: '项目名称', value: this.commInfo.projectName || '--'},
                {text: '基础电价', value: this.commInfo.basePrice.toString() || '--'},
                {text: '提成电价', value: this.commInfo.upPrice.toString() || '--'},
                {text: '小区名称', value: this.commInfo.communityName || '--'},
                {text: '小区地址', value: this.commInfo.projectAddr || '--'},
                {text: '物业名称', value: this.commInfo.companyName || '--'},
                {text: '联系人员', value: this.commInfo.contact || '--'},
                {text: '联系电话', value: this.commInfo.contactTelphone || '--'},
                {text: '创建时间', value: this.commInfo.createTime || '--'},
                {text: '包月信息', value: this.commInfo.monthly ? this.commInfo.monthly.monthlyPrice + '元/' + this.commInfo.monthly.monthlyTime+'小时，共计可充' + this.commInfo.monthly.monthlyCnt+'次' : '--'},
                {text: '临充信息', value: this.immPrice || '--'},
                {text: '备注信息', value: this.commInfo.remark || '--'}
             ]
           }
          },

        },
        created: function () {
          for(let key in this.row) {
            this.$set(this.commInfo, key, this.row[key])
          }
          this.getDeviceInfo();
          this.makePie(1);
          this.tableShow = true;
        }
    }
</script>
<style lang="scss" scoped="scoped">
   $LInfoC: 16em;
  .info-container{
     .rInfoCon{
       width: calc(100% - #{$LInfoC} - 2em - 2px);
       float:left;
       //border-left:1px solid rgba(255,255,255,0.15)
     }
     .lInfoCon{
       width: $LInfoC;
       float:left;
       .infoL {
          width: calc(100% - 5em);
          display:table;
          padding:0.5em 2em 0.5em 3em;
          border-right: 1px solid rgba(255,255,255,0.15);
          flex: 1;
          margin-top:0.3em;
          min-height: 7em;
          margin-bottom: 1em;
          .infoLCon{
             display:table-cell;
             margin: auto;
             vertical-align: middle;
             img{
               width: 2.5em;
               height: 2.8em;
               float:left;
               margin-top:0.2em;
               margin-right:0.5em;
             }
             div{
               float:left;
               width: calc(100% - 3em);
               height:100%;
             }
            span{
              display: block;
              float:left;
              width: 100%;
              padding-left: 1em;
              font-size: 0.83em;
              text-align:left;
              overflow: hidden;
              white-space: nowrap;
              text-overflow: ellipsis;
            }
            span.hasColor{
              color: #3E8EDE;
              font-size: 1.8em;
              font-weight: 600
            }
         }
       }
     }
     .chartCon{
      .simpleTopTab{
        border-bottom: 1px solid rgba(255,255,255,0.15);
        font-size: 0.83em;
        li{
           padding: 0.6em 1.5em;
           float: left;
           cursor: pointer;
           color: white;
           background:rgba(#305ea0, 1);
           border-right: 1px solid rgba(255,255,255,0.15);
           position:relative
        }

        li:last-child{
          border-right:0
        }
        li:hover{
          background: rgba(255,255,255, 0.1);
        }
        li.selected {
           background: #227fdf;
        }
        li:hover:after, li.selected:after{
          content: '';
          position:absolute;
          width:100%;
          height:0.2em;
          background:  #227fdf;
          bottom:0;
          left:0
        }
      }
     .simpleTopTab:after{
        clear:both;
        content:'';
        display:block;
        width:1px;
        height:0;
    }
    .tabCenContent{
        width:100%;
        height: calc(100% - 2em);
        display:flex;
        position: relative;
        
        .lpiechart{
           width: calc((100% - 0em) * 0.5);
           height:100%;
           position: relative;
          .pieLegendDiv{
            width: 100%;
            height:2em;
            position:relative;
            .legendCon{
              position: absolute;
              padding:0.9em 0 0 0;
              top: 0%;
              //left:50%;
              width:100%;
             // transform: translate(0%, -50%); 
              .legendSpanCon{
                  position: relative;
                  margin:0 0.5em;
                  min-width: 4.5em;
                  cursor: pointer;
                  display: inline-block;
                  span.legendSpan{
                    display: block;
                    font-size:0.84em;
                    height:100%;
                    text-align:left;
                    padding-left:2.2em;
                    margin:0 0.5em
                  }
                  span.legendSpanBefore{
                   display: block;
                   position: absolute;
                   height:0.75em;
                   width: 1.6em;
                   border-radius: 0.2em;
                   top: 15%;
                   left: 0em;
                   background: rgba(255,255,255,0.4);
                 }
              }
              
              
           }
          }
          .piechart{
              width: 100%;
              height: calc(100% - 2em)
          }
        }
        .rtableDetail{
           width:calc((100% - 0em) * 0.5);
           height:calc(100% - 1.6em);
           margin:0.8em 0!important;
           font-size:0.84em;
           p{
            text-align: left
         }
        }
      }
  }  
}
  
</style>



